import React, { useState } from "react";
import arrow from "@styles/assets/images/crypto-trading-bot/arrow.png";
import { StaticImage } from "gatsby-plugin-image";

export const CryptoBotCard = ({ data, isOpen, onClick }) => {
  return (
    <div className={`${isOpen ? "crypto-bot-card-open" : "crypto-bot-card"}`}>
      <button
        onClick={onClick}
        className={`${
          isOpen ? "crypto-bot-card-head-none" : "crypto-bot-card-head"
        }`}
      >
        <h5 className="about-h5">{data.title}</h5>
        <div className={`${isOpen ? "rotate" : "arrow"}`}>
          {" "}
          <img src={arrow} alt="arrow" />
        </div>
      </button>
      {isOpen && (
        <div className="crypto-bot-card-content">
          <p className="about-p">{data.content}</p>
        </div>
      )}
    </div>
  );
};

const datas = [
  {
    id: 0,
    title: "Arbitrage Bot Development",
    content:
      "Arbitrage difference in capture prices between exchanges using arbitrage bots that are precision built. We architecture systems which are multi-exchange connected, uses order-book data and are latency optimized. The use of smart routing, slippery, and risk thresholds keep all trades profitable during the real market situations.",
  },
  {
    id: 1,
    title: "DCA Bot Development",
    content:
      "AutoDollarcost averaging in order to make the long time investment simple. Recurring buys, portfolio rebalancing and value-averaging are managed by our DCA bots. Constructed to be highly dependable and because of the easily readable dashboards, traders can implement consistent strategies free of emotion.",
  },
  {
    id: 2,
    title: "Sniper Bot Development",
    content:
      "Sniper bots to toke launches and liquidity events with and up to high speed. They include that of micro-level monitoring, gas efficiency and enhancement of transaction priority. Bots are enhanced with anti-slip measures and a security of accurate execution.",
  },
  {
    id: 3,
    title: "MEV Bot Development",
    content:
      "Get the most out of on-chain by automating responsible MEV. Our MEV bots read through mempools, package transactions and at the management of private relays. Our priorities are ethics and visible implementation that combines inbuilt frontrunning hedge and auditable performance indicators.",
  },
];

const CryptoBotDev = () => {
  const [openId, setOpenId] = useState(null);
  const handleToggle = (id) => [
    setOpenId((prevId) => (prevId === id ? null : id)),
  ];
  return (
    <div className="crypto-bot-dev flex">
      <div className=" crypto-bot-head item-center">
        <h2 className="about-h2">
          Discover Our Full Suite of Crypto Trading Bot Development Solutions
        </h2>
        <div className="crypto-bot-coin-image">
          <StaticImage
            src="../../styles/assets/images/crypto-trading-bot/crypto-coins.png"
            alt="crypto-coins"
            layout="constrained"
            quality={75}
            loading="eager"
            placeholder="none"
          />
        </div>
      </div>
      <div className="crypto-bot-cards flex-column">
        {datas.map((data) => (
          <CryptoBotCard
            key={data.id}
            data={data}
            isOpen={openId === data.id}
            onClick={() => handleToggle(data.id)}
          />
        ))}
      </div>
    </div>
  );
};

export default CryptoBotDev;
